html {
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 20px;
  line-height: 200%;
  font-weight: 400;
  background-color: #170409;
  color: #fff;
}
body.lock {
  overflow-y: hidden;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
}
button .icon {
  width: 24px;
  height: 24px;
}

h1,
h2,
h4,
h3,
ul,
li,
p {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

input {
  outline: transparent;
}

figure {
  margin: 0;
}

.wrapper {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.wrapper .container {
  max-width: 1400px;
  width: 100%;
  padding: 0 40px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .wrapper .container {
    padding: 0 20px;
  }
}
@media (max-width: t) {
  .wrapper .container else {
    padding: 0 20px;
  }
}
@media (max-width: 400px) {
  .wrapper .container {
    padding: 0 10px;
  }
}
@media (max-width: m) {
  .wrapper .container else {
    padding: 0 10px;
  }
}
.wrapper .header {
  height: 90px;
}
.wrapper .main {
  flex: 1;
  padding: 50px 0;
}

.header {
  background-color: #e33258;
}
.header .header__container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .header__container .logo-img {
  display: block;
  height: 50px;
}
@media (max-width: 400px) {
  .header .header__container .logo-img {
    height: 35px;
  }
}
@media (max-width: m) {
  .header .header__container .logo-img else {
    height: 35px;
  }
}
.header .header__container .form-wrapper {
  position: relative;
  margin-left: 50px;
}
@media (max-width: 768px) {
  .header .header__container .form-wrapper {
    width: 100%;
  }
}
@media (max-width: t) {
  .header .header__container .form-wrapper else {
    width: 100%;
  }
}
@media (max-width: 500px) {
  .header .header__container .form-wrapper {
    margin-left: 20px;
  }
}
@media (max-width: wm) {
  .header .header__container .form-wrapper else {
    margin-left: 20px;
  }
}
@media (max-width: 768px) {
  .header .header__container .form-wrapper .form {
    opacity: 0;
    transition: opacity 0.5s ease;
  }
}
@media (max-width: t) {
  .header .header__container .form-wrapper .form else {
    opacity: 0;
    transition: opacity 0.5s ease;
  }
}
.header .header__container .form-wrapper .show-form {
  position: absolute;
  top: 4px;
  right: 0;
  display: none;
  background-color: transparent;
  border: none;
}
.header .header__container .form-wrapper .show-form .icon {
  fill: white;
}
@media (max-width: 768px) {
  .header .header__container .form-wrapper .show-form {
    display: block;
  }
}
@media (max-width: t) {
  .header .header__container .form-wrapper .show-form else {
    display: block;
  }
}
.header .header__container .form-wrapper.open .form {
  display: flex;
  width: 100%;
  opacity: 1;
  transition: opacity 0.5s ease;
}
.header .header__container .form-wrapper.open .show-form {
  display: none;
}

.footer {
  font-size: 14px;
  opacity: 0.8;
  border-top: 1px solid #989898;
}
.footer .footer__container {
  padding-top: 20px;
  padding-bottom: 20px;
}
.footer .footer__container .footer__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer .footer__container .footer__rs-logo {
  display: block;
  width: 100px;
}
.footer a:hover {
  text-decoration: underline;
}

.form {
  display: flex;
  width: max-content;
  border-radius: 4px;
  overflow: hidden;
  background-color: #fff;
}
@media (max-width: 768px) {
  .form {
    width: 100%;
  }
}
@media (max-width: t) {
  .form else {
    width: 100%;
  }
}
.form .input,
.form .button {
  border: none;
}
.form .input {
  padding: 7px 10px;
  width: 300px;
  font-size: 18px;
  background-color: transparent;
}
.form .input::placeholder {
  font-size: 14px;
  opacity: 0.75;
}
@media (max-width: 768px) {
  .form .input {
    width: 100%;
  }
}
@media (max-width: t) {
  .form .input else {
    width: 100%;
  }
}
.form .button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}
.form .button ~ .button {
  margin-left: 5px;
}
@media (max-width: 500px) {
  .form .button {
    padding: 0 2px;
  }
}
@media (max-width: wm) {
  .form .button else {
    padding: 0 2px;
  }
}
.form .icon {
  width: 24px;
  height: 24px;
  fill: #989898;
  transition: fill 0.3s ease;
}
.form .icon:hover {
  fill: #000;
}
.form .button-reset .icon {
  transform: scale(0.75);
}
.form .button-reset .icon .svg {
  transform: scale(0.25);
}

.app .loader {
  display: none;
}
.app.loading .app-content {
  display: none;
}
.app.loading .loader {
  display: block;
}

.loader {
  color: #ffffff;
  font-size: 10px;
  margin: 150px auto;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load4 1.3s infinite linear;
  animation: load4 1.3s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}

