.app .app__title {
  font-size: 40px;
}
@media (max-width: 400px) {
  .app .app__title {
    margin-top: 0;
    font-size: 32px;
  }
}
@media (max-width: m) {
  .app .app__title else {
    margin-top: 0;
    font-size: 32px;
  }
}
.app .film__items {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 40px;
}
@media (max-width: 1100px) {
  .app .film__items {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: n) {
  .app .film__items else {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 768px) {
  .app .film__items {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: t) {
  .app .film__items else {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 400px) {
  .app .film__items {
    grid-template-columns: 1fr;
  }
}
@media (max-width: m) {
  .app .film__items else {
    grid-template-columns: 1fr;
  }
}
.app .film__items .film__item {
  position: relative;
  cursor: pointer;
}
.app .film__items .film__item .poster-overflow {
  position: relative;
  border-radius: 5px;
  margin-bottom: 10px;
  overflow: hidden;
}
.app .film__items .film__item .poster-overflow::after {
  position: absolute;
  z-index: 2;
  display: block;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease;
}
.app .film__items .film__item .poster-overflow .poster-wrapper {
  position: relative;
  display: block;
  background-color: rgba(254, 206, 168, 0.2);
  display: block;
  position: relative;
  overflow: hidden;
}
.app .film__items .film__item .poster-overflow .poster-wrapper:before {
  display: block;
  content: "";
  width: 100%;
  padding: 150% 0 0 0;
  z-index: 1;
}
.app .film__items .film__item .poster-overflow .poster-wrapper img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  font-family: "object-fit: cover";
}
.app .film__items .film__item .title {
  margin-bottom: 5px;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 1px;
  transition: color 0.3s ease;
  line-height: 1.2;
  max-height: 2.4;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.app .film__items .film__item .info {
  font-size: 15px;
}
.app .film__items .film__item .info .info__year ~ .info__genre::before {
  display: inline-block;
  content: ", ";
}
.app .film__items .film__item:hover .poster-overflow::after {
  background-color: transparent;
}
.app .film__items .film__item:hover .title {
  color: #ff847c;
}
.app .film__items .film__item .rating {
  position: absolute;
  z-index: 50;
  top: 10px;
  left: -10px;
  width: max-content;
  padding: 8px 15px;
  border-radius: 5px;
  font-size: 16px;
  line-height: 1;
}
.app .film__items .film__item .rating.rating__high {
  background-color: #e33258;
}
.app .film__items .film__item .rating.rating__mid {
  background-color: #ffb348;
}
.app .film__items .film__item .rating.rating__low {
  background-color: #989898;
}

.film {
  display: grid;
  gap: 0 40px;
  grid-template-columns: 300px 1fr auto;
  grid-template-rows: max-content max-content 1fr;
  grid-template-areas: "poster title rate" "poster genres ." "poster overview .";
}
@media (max-width: 1100px) {
  .film {
    grid-template-columns: 300px 1fr;
    grid-template-rows: max-content max-content 1fr max-content;
    grid-template-areas: "poster title" "poster genres" "poster overview" "poster rate";
  }
}
@media (max-width: n) {
  .film else {
    grid-template-columns: 300px 1fr;
    grid-template-rows: max-content max-content 1fr max-content;
    grid-template-areas: "poster title" "poster genres" "poster overview" "poster rate";
  }
}
@media (max-width: 768px) {
  .film {
    grid-template-columns: 200px 1fr;
    gap: 0 20px;
  }
}
@media (max-width: t) {
  .film else {
    grid-template-columns: 200px 1fr;
    gap: 0 20px;
  }
}
@media (max-width: 500px) {
  .film {
    grid-template-columns: 1fr;
    grid-template-areas: "title" "rate" "poster" "genres" "overview";
  }
}
@media (max-width: wm) {
  .film else {
    grid-template-columns: 1fr;
    grid-template-areas: "title" "rate" "poster" "genres" "overview";
