.film .poster-wrapper {
  grid-area: poster;
  height: max-content;
  display: block;
  background-color: rgba(254, 206, 168, 0.2);
  display: block;
  position: relative;
  overflow: hidden;
}
.film .poster-wrapper:before {
  display: block;
  content: "";
  width: 100%;
  padding: 150% 0 0 0;
  z-index: 1;
}
.film .poster-wrapper img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  font-family: "object-fit: cover";
}
.film .title-wrapper {
  grid-auto-rows: title;
}
.film .title-wrapper .title {
  margin-bottom: 10px;
  font-size: 40px;
  line-height: 50px;
}
@media (max-width: 768px) {
  .film .title-wrapper .title {
    margin-bottom: 0;
    font-size: 30px;
    line-height: 40px;
  }
}
@media (max-width: t) {
  .film .title-wrapper .title else {
    margin-bottom: 0;
    font-size: 30px;
    line-height: 40px;
  }
